:root {
  --primary: 222 47% 31%;
  --primary-foreground: 210 40% 98%;
  --secondary: 160 84% 39%;
  --secondary-foreground: 210 40% 98%;
  --background: 210 40% 98%;
  --foreground: 222 84% 5%;
  --card: 0 0% 100%;
  --card-foreground: 215 25% 27%;
  --popover: 0 0% 100%;
  --popover-foreground: 222 84% 5%;
  --muted: 210 40% 96%;
  --muted-foreground: 215 16% 47%;
  --accent: 210 40% 96%;
  --accent-foreground: 222 84% 5%;
  --border: 214 32% 91%;
  --input: 214 32% 91%;
  --ring: 222 47% 31%;
  --destructive: 0 84% 60%;
  --destructive-foreground: 210 40% 98%;
  --success: 160 84% 39%;
  --success-foreground: 210 40% 98%;
  --radius: 0.75rem;
  --glass-bg: rgba(255, 255, 255, 0.7);
  --glass-border: rgba(255, 255, 255, 0.3);
  --glass-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.15);
}
.dark {
  --primary: 217 91% 60%;
  --primary-foreground: 222 84% 5%;
  --secondary: 160 84% 45%;
  --secondary-foreground: 222 84% 5%;
  --background: 222 84% 5%;
  --foreground: 210 40% 98%;
  --card: 222 47% 11%;
  --card-foreground: 210 40% 98%;
  --popover: 222 47% 11%;
  --popover-foreground: 210 40% 98%;
  --muted: 217 33% 17%;
  --muted-foreground: 215 20% 65%;
  --accent: 217 33% 17%;
  --accent-foreground: 210 40% 98%;
  --border: 217 33% 17%;
  --input: 217 33% 17%;
  --ring: 217 91% 60%;
  --destructive: 0 62% 50%;
  --destructive-foreground: 210 40% 98%;
  --success: 160 84% 45%;
  --success-foreground: 222 84% 5%;
  --glass-bg: rgba(15, 23, 42, 0.7);
  --glass-border: rgba(255, 255, 255, 0.1);
  --glass-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.4);
}
* {
  border-color: hsl(var(--border));
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: hsl(var(--background));
  color: hsl(var(--foreground));
  font-feature-settings: "rlig" 1, "calt" 1;
  transition: background-color 0.3s ease, color 0.3s ease;
}
h1, h2, h3, h4, h5, h6 {
  color: hsl(var(--foreground));
  font-weight: 700;
  line-height: 1.2;
}
h1 {
  font-size: 2rem;
  font-family: var(--font-merriweather), serif;
}
h2 {
  font-size: 1.5rem;
  font-family: var(--font-merriweather), serif;
}
h3 {
  font-size: 1.125rem;
  font-weight: 600;
}
.glass {
  background: var(--glass-bg);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border: 1px solid var(--glass-border);
  box-shadow: var(--glass-shadow);
}
.glass-card {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: 1rem;
  box-shadow: var(--glass-shadow);
  transition: all 0.3s ease;
}
.glass-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(31, 38, 135, 0.2);
}
.glass-button {
  background: var(--glass-bg);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid var(--glass-border);
  transition: all 0.3s ease;
}
.glass-button:hover {
  background: rgba(255, 255, 255, 0.9);
  transform: scale(1.02);
}
.dark .glass-button:hover {
  background: rgba(30, 41, 59, 0.9);
}
.glass-input {
  background: var(--glass-bg);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid var(--glass-border);
  transition: all 0.3s ease;
}
.glass-input:focus {
  border-color: hsl(var(--primary));
  box-shadow: 0 0 0 3px hsl(var(--primary) / 0.2);
}
.gradient-bg {
  background: linear-gradient(-45deg, #1e3a8a, #3b82f6, #10b981, #059669);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}
.gradient-text {
  background: linear-gradient(135deg, hsl(var(--primary)), hsl(var(--secondary)));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.gradient-border {
  position: relative;
  background: hsl(var(--card));
  border-radius: 1rem;
}
.gradient-border::before {
  content: '';
  position: absolute;
  inset: -2px;
  background: linear-gradient(135deg, hsl(var(--primary)), hsl(var(--secondary)));
  border-radius: inherit;
  z-index: -1;
}
.animate-float {
  animation: float 6s ease-in-out infinite;
}
.animate-pulse-glow {
  animation: pulseGlow 2s ease-in-out infinite;
}
.animate-slide-up {
  animation: slideUp 0.5s ease-out forwards;
}
.animate-slide-down {
  animation: slideDown 0.5s ease-out forwards;
}
.animate-scale-in {
  animation: scaleIn 0.3s ease-out forwards;
}
.animate-bounce-soft {
  animation: bounceSoft 2s ease-in-out infinite;
}
.animate-shimmer {
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.4), transparent);
  background-size: 200% 100%;
  animation: shimmer 2s infinite;
}
.animate-spin-slow {
  animation: spin-slow 8s linear infinite;
}
.stagger-1 {
  animation-delay: 0.1s;
}
.stagger-2 {
  animation-delay: 0.2s;
}
.stagger-3 {
  animation-delay: 0.3s;
}
.stagger-4 {
  animation-delay: 0.4s;
}
.stagger-5 {
  animation-delay: 0.5s;
}
.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.hover-lift:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}
.hover-scale {
  transition: transform 0.3s ease;
}
.hover-scale:hover {
  transform: scale(1.05);
}
.hover-glow {
  transition: box-shadow 0.3s ease;
}
.hover-glow:hover {
  box-shadow: 0 0 30px hsl(var(--primary) / 0.4);
}
.btn-primary {
  background: linear-gradient(135deg, hsl(var(--primary)) 0%, hsl(222 47% 25%) 100%);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}
.btn-primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s ease;
}
.btn-primary:hover::before {
  left: 100%;
}
.btn-primary:hover {
  transform: scale(1.02);
  box-shadow: 0 8px 25px -8px hsl(var(--primary) / 0.5);
}
.btn-primary:active {
  transform: scale(0.98);
}
.card-enhanced {
  background: hsl(var(--card));
  border: 1px solid hsl(var(--border));
  border-radius: calc(var(--radius) + 4px);
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1);
  transition: all 0.3s ease;
}
.card-enhanced:hover {
  box-shadow: 0 10px 40px -10px rgb(0 0 0 / 0.2);
  transform: translateY(-4px);
}
.skeleton {
  background: linear-gradient(90deg, hsl(var(--muted)) 25%, hsl(var(--muted-foreground) / 0.1) 50%, hsl(var(--muted)) 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
  border-radius: 0.5rem;
